@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('./custom.css');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,500;1,800&display=swap');

@layer base {
  html,body{
    font-family: 'Montserrat', sans-serif !important;
    overflow:auto !important;
    }

    *{
      font-family: 'Montserrat', sans-serif !important;
    }
  
  li {
    @apply p-5
  }
  .mainbutton {
    @apply inline-block px-12 py-2.5 text-metal bg-grey font-medium text-lg leading-tight shadow-xl hover:shadow-lg focus:outline-none focus:ring-0 active:shadow-lg transition duration-200 ease-in-out
  }
  .cart {
    @apply text-white border bg-grey border-grey px-4 py-2 mx-2
  }
  .badge-icon{
    @apply inline-block py-1 px-1.5 text-xs leading-none text-center whitespace-nowrap align-baseline font-bold bg-metal text-white rounded-full ml-2
  }
  .search-form{
    @apply form-control
    flex
    w-full
    px-5
    py-2
    text-lightgray
    font-normal
    bg-clip-padding
    transition
    ease-in-out
    m-0
    bg-gray-300 border-gray-900 outline-inherit ring-0
  }
  
  .product-form{
    @apply form-control
    flex
    w-full
    px-5
    py-3
    text-lightgray
    font-normal
    bg-clip-padding
    transition
    ease-in-out
    m-0
    focus:text-lightgray bg-gray-300 border-gray-300 focus:outline-inherit focus:ring-0
  }

  .selectproduct-form{
    @apply form-select appearance-none block
    w-full
    px-5
    py-3
    text-lightgray
    font-normal
    bg-clip-padding bg-no-repeat
    transition
    ease-in-out
    m-0
    focus:text-lightgray bg-gray-300 border-gray-300 focus:outline-inherit focus:ring-0
  }

  .labelling{
    @apply inline-block mb-2 text-gray-700
  }

  .select-form{
    @apply form-select appearance-none block
    w-full
    px-5
    py-2
    text-lightgray
    font-normal
    bg-gray-300 bg-clip-padding bg-no-repeat
    transition
    ease-in-out
    m-0
    focus:text-lightgray focus:bg-gray-300 border-gray-300 focus:outline-inherit focus:ring-0
  }

  .metal-style{
    @apply bg-metal border-metal text-white
  }
  .header{
    @apply w-full md:text-5xl text-2xl font-bold pb-10 text-lightgray text-center
  }
  .animated-button{
    @apply inline-block px-12 py-2.5 bg-metal text-white font-medium text-lg leading-tight shadow-md hover:bg-[#58a713] hover:shadow-lg focus:bg-[#58a713] focus:shadow-lg focus:outline-none focus:ring-0 active:bg-[#13a731] active:shadow-lg transition duration-200 ease-in-out
  }
  .hit{
    @apply overflow-x-auto overflow-scroll
  }
  .active-step{
    @apply grow
    inline-flex
    items-center
    justify-center
    w-1/2
    py-3
    font-medium
    leading-none
    tracking-wider
    text-metal
    bg-gray-100
    border-b-2 border-metal border-r-2
    rounded-t
    sm:px-6 sm:w-auto sm:justify-start
  }
  .inactive-step{
    @apply grow
    inline-flex
    items-center
    justify-center
    w-1/2
    py-3
    font-medium
    leading-none
    tracking-wider
    border-b-2 border-gray-200
    sm:px-6 sm:w-auto sm:justify-start
    hover:text-gray-900
  }
  .stripe-form{
    @apply form-control
    w-full
    px-5
    py-2
    text-lightgray
    font-normal
    bg-grey bg-clip-padding
    transition
    ease-in-out
    m-0
    focus:text-lightgray focus:bg-gray-300 focus:border-gray-300 focus:outline-inherit focus:ring-0
  }
}